import React, { Component, PropTypes as types } from 'react'
import { omit, defaultsDeep } from 'lodash'
import jclrz from 'json-colorz'

export class App extends Component {
  static childContextTypes = {
    project: types.object,
    screen: types.object,
    settings: types.object,
    processes: types.object,
    log: types.func
  }

  constructor(args = {}) {
    let { options, project, screen } = args
    let props = omit(args, 'screen', 'project', 'options')

    super(props)

    this.project = project
    this.screen = screen
    this.log = screen.log.bind(screen)

    this.state = {}
  }

  getChildContext () {
    return {
      project: this.project,
      screen: this.screen,
      settings: this.project.settings,
      log: this.log,
      processes: this.processes
    }
  }

  componentDidMount() {
    jclrz(this.project, (output) => {
      this.setState({output})
    })
  }

  render () {
    return(
      <box label='skypager' {...(borderStyles({color: 'magenta', style: 'line'}))}>
        {this.state && this.state.output}
      </box>
    )
  }
}

export default App


function borderStyles(options = {style: 'line', color: 'white'}) {
  let {style, color} = options

  return {
    border : {
      type: style
    },
    style : {
      border: {
        fg: color
      }
    }
  }
}



